.contact {
  margin-top: 50px;
  padding-left: 2px;

  h1 {
    position: relative;
    font-size: 3.6rem;
    font-family: Karla;
    line-height: 1;
    padding-left: 60px;
  }

  p {
    white-space: pre-line;
    text-align: left;
    margin-top: 30px;
    max-width: 328px;
    width: 100%;
    font-size: 2.5rem;
    font-family: GoogleSans;
    line-height: 40px;
  }

  .email {
    border-bottom: 3px solid #F82929;
  }
  
  .li {
    background-image: url("../assets/images/mail-orange.svg");
  }

  .dr {
    background-image: url("../assets/images/mail.svg");
  }

}