.me {
  user-select: none;
  margin-left: -30px;
 

  .text_me {
    padding-left: 30px;
    border-top-left-radius: 1px;
    border-top-color: transparent;
    // border-left: 3px solid #218C9A; // #218C9A #f07626
  }

  .dr {
    border-left: 3px solid #298AF8; // #218C9A #f07626 ##298AF8 #21609a
  }
  .li {
    border-left: 3px solid rgb(255, 126, 21);
  }

  h1 {
    line-height: 1;
    font-size: 6rem;
    font-family: OpenSans;
    margin-top: 55px;
    padding: 0;
    text-align: left;
    margin-left: -3px;
  }

  p {
    margin-top: 30px;
    max-width: 450px;
    text-align: left;
    width: 100%;
    font-family: GoogleSans;
    font-size: 2.5rem;
    line-height: 2;
  }

  .js {
    padding-bottom: 2px;
    border-bottom: 3px solid #FFE600 ; // #FFE600 #FFB300
    cursor: default;
  }

  .rc {
    padding-bottom: 2px;
    border-bottom: 3px solid #54C2EB;
    cursor: default
  }
}




