.about {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  padding-left: 30px;
  padding-right: 10px;
  grid-template-areas: 'me''projects''hello';
}

.me {
  grid-area: me;
  justify-self: start;
}

.projects {
  grid-area: projects;
  justify-self: end;
  padding-bottom: 10px;
}

.contact {
  grid-area: hello;
  justify-self: start;
}

@media (max-width: 665px) {
  .projects {
    justify-self: start;
  }

}