html {
  font-size: 55%;
}

html,
body {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}


@font-face {
  font-family: OpenSans;
  src: url('../../assets/fonts/OpenSans-SemiBold.ttf');
}

@font-face {
  font-family: GoogleSans;
  src: url('../../assets/fonts/ProductSans-Regular.ttf');
}

@font-face {
  font-family: Karla;
  src: url('../../assets/fonts/Karla-Bold.ttf');
}


#root {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

.app {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  height: 100%;
  min-width: 320px;
  overflow-x: hidden;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: 'wrapper';
  grid-row-gap: 0px;
  padding: 0px 50px;
}

.wrapper {
  grid-area: wrapper;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  max-width: 920px;
  width: 100%;
  font-size: 1.6rem;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: 'header''content';
  grid-row-gap: 0px;
  // padding: 30px;

}

.light {
  color: #000000;
  background-color: #ffffff;
}

.dark {
  color: #ffffff;
  background-color: #212731; //#212731 #171b20;
}

header {
  width: 100%;
  justify-self: stretch;
  height: auto;
  grid-area: header;
}

main {
  grid-area: content;
  justify-self: stretch;
  width: 100%;
  overflow-x: hidden;
 
}

a {
  color: inherit;
  font-size: 2.5rem;
  text-decoration: none;
}

.icon {
  display: block;
  background-repeat: no-repeat;
  background-position-y: center;
  background-size: contain;
  height: 3.6rem;
  float: left;
  width: 40px;
}

@media (max-width: 665px) {
  html {
    font-size: 55%;
  }
  .text_me h1 > span {
    display: block;
  }

}

@media (max-width: 600px) {
  html {
    font-size: 50%;
  }

  .me .dr {
    border-left: 2px solid rgb(33, 96, 154); // #218C9A #f07626
  }
  .me .li {
    border-left: 2px solid #F82929;
    border-radius: 1px
  }

  .me .js {
    border-bottom: 2px solid #FFB300;
    cursor: default;
  }

  .me .rc {
    border-bottom: 2px solid #54C2EB;
    cursor: default
  }
  .contact .email {
    border-bottom: 2px solid #F82929;
  }

  .projects  p > a > span {
    border-bottom: 2px solid #F82929;
  }
    .projects p > a {
    display: block;
  }
}

@media (max-width: 445px) {
  html {
    font-size: 45%;
  }

  .app {
    padding: 0 30px;
  }
}

@media (max-width: 415px) {
  .projects p > a {
    display: block;
  }

  .app {
    padding: 0px 20px;
  }
}