.projects {
  margin-top: 70px;
  position: relative;

  h1 {
    font-size: 3.6rem;
    line-height: 1;
    font-family: Karla;
    padding-left: 60px;
  
  }

  p {
    margin-top: 30px;
    font-size: 2.5rem;
    font-family: GoogleSans;
    line-height: 40px;
    max-width: 320px;
    width: 100%;
    text-align: left;
  }

  p > a > span {
    width: 100%;
    // display: block;
    padding-bottom: 5px;
    border-bottom: 3px solid #F82929;
  }
}

.projects > .li {
  background-image: url("../assets/images/code-orange.svg");
}

.projects > .dr {
  background-image: url("../assets/images/code.svg");
}
